* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

header {
  width: 100%;
  height: 8vh;
  background-color: #292828;
}
header nav {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-wrap: none;
}
header nav .header_logo {
  color: #ffffff;
}
header nav .nav_list {
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: none;
}
@media (max-width: 900px) {
  header nav .nav_list {
    position: absolute;
    top: 8vh;
    right: 0;
    width: 50vh;
    height: 92vh;
    background-color: #292828;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: none;
    opacity: 0;
    transition: opacity 0.5s ease;
  }
}
header nav .nav_list li {
  margin-left: 30px;
}
@media (max-width: 900px) {
  header nav .nav_list li {
    margin-left: 0;
  }
}
header nav .nav_list li a {
  color: #ffffff;
}
header nav .nav_list.active {
  opacity: 1;
}
header nav .mobile_menu {
  cursor: pointer;
  display: none;
}
@media (max-width: 900px) {
  header nav .mobile_menu {
    display: block;
  }
}
header nav .mobile_menu div {
  height: 2px;
  width: 30px;
  margin: 8px;
  background-color: #ffffff;
  transition: 0.5s;
}
header nav .mobile_menu.active .line1 {
  transform: rotate(-45deg) translate(-8px, 8px);
}
header nav .mobile_menu.active .line2 {
  opacity: 0;
}
header nav .mobile_menu.active .line3 {
  transform: rotate(45deg) translate(-5px, -7px);
}

